// app/page.js

// This is a Server Component by default in the App Router.
// It will be rendered on the server for the initial load.
export default function HomePage() {
const [isGameActive, setIsGameActive] = useState(false);

  // Function to transition from Menu to Game
  const handleStartGame = () => {
    setIsGameActive(true);
  };

  // Add a listener for the SPACE key, as specified in the original HTML
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.code === 'Space' && !isGameActive) {
        handleStartGame();
      }
    };
    
    document.addEventListener('keydown', handleKeyDown);

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [isGameActive]); // Re-run effect if isGameActive changes

  return (
    <div>
      {/* Conditionally render the Menu or the Game */}
      {!isGameActive && (
        <Menu onStartGame={handleStartGame} />
      )}
      
      {isGameActive && (
        <Game />
      )}
    </div>
  );
}
